import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import CountryTab from "./CountryTab";
import Boxheading from "../Common/Boxheading";

const DeviceTab = () => {
  return (
    <>
      <div className="box-shadow-height global-tabbing ">
      <Boxheading title="Devices" />
        <Tabs
          defaultActiveKey="Browser"
          id="uncontrolled-tab-example"
          // className="mb-3"
        >
          <Tab eventKey="Browser" title="Browser">
            <CountryTab apiname="browserreport" title="Browser" />
          </Tab>
          <Tab eventKey="OS" title="OS">
            <CountryTab apiname="osreport" title="OS" />
          </Tab>
          <Tab eventKey="Size" title="Size">
            <CountryTab apiname="devicereport" title="Size" />
          </Tab>
        </Tabs>
      </div>
    </>
  );
};

export default DeviceTab;
