import axios from "axios";
import React, { useEffect, useState } from "react";
import { CommonServices } from "../Common/CommonService";
import { useSelector } from "react-redux";
import Table from "react-bootstrap/Table";
import EngagementTableModal from "../Modals/EngagementTableModal";
import Boxheading from "../Common/Boxheading";
import LandingPageModal from "../Modals/LandingPageModal";
import NoData from "./NoData";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";
import RightArrow from "../Assets/RightArrow";
// import LandingPageModal from "../Mo";

const LandingPageTable = () => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [EngagementDataLoader, setEngagementDataLoader] = useState(false);
  const [EngagementModalShow, setEngagementModalShow] = useState(false);
  const [EngagementData, setEngagementData] = useState([]);
  async function getEngagementReport() {
    setEngagementDataLoader(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.LANDINGPAGEREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse, setEngagementDataLoader);
    if (response?.isValidate) {
      setEngagementData(response.data);
    }
    if (!response?.isValidate) {
    }
    // try {
    //   setEngagementDataLoader(true);
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   axios
    //     .get(`http://localhost:8000/api/landingpagereport`, { params: body })
    //     .then((response) => {
    //       setEngagementData(response.data);
    //       setEngagementDataLoader(false);
    //     });
    // } catch (error) {
    //   setEngagementDataLoader(false);
    //   toast.error(error?.response?.data?.message + "in landingpagereport.");
    // }
  }
  useEffect(() => {
    if (DateFilterValue) {
      getEngagementReport();
    }
  }, [DateFilterValue]);

  return (
    <>
      {/* <div className="box-shadow"> */}
      {/* <Boxheading title="Pages and screens" /> */}
      {EngagementDataLoader ? (
        <LoadingComp />
      ) : (
        <>
          <Table striped hover responsive>
            <thead>
              <tr>
                <th>Landing page</th>
                <th>Sessions</th>
                <th>Users</th>
                <th>New Users</th>
                <th>Average Engagement time per session</th>
                <th>Key Events</th>
                <th>Total Revenue</th>
              </tr>
            </thead>
            <tbody>
              {EngagementData?.length > 0 ? (
                EngagementData?.map(
                  (item_e, index_e) =>
                    index_e < 8 && (
                      <tr key={index_e}>
                        <td>{item_e?.dimension}</td>
                        <td>
                          {CommonServices.NumberCalculateValue(
                            item_e.metrics.sessions
                          )}
                        </td>
                        <td>
                          {CommonServices.NumberCalculateValue(
                            item_e.metrics.activeUsers
                          )}
                        </td>
                        <td>
                          {CommonServices.NumberCalculateValue(
                            item_e.metrics.newUsers
                          )}
                        </td>
                        <td>
                          {CommonServices.TimestampConverter(
                            item_e.metrics.averageEngagementTimePerSession
                          )}
                        </td>
                        <td>
                          {CommonServices.NumberCalculateValue(
                            item_e.metrics.keyevents
                          )}
                        </td>
                        <td>
                          ${parseFloat(item_e.metrics.totalRevenue).toFixed(2)}
                        </td>
                      </tr>
                    )
                )
              ) : (
                <NoData type="table" />
              )}
            </tbody>
          </Table>
          {EngagementData?.length > 8 && (
              <div className="text-end">
            <div
              onClick={() => setEngagementModalShow(true)}
              className="blue-text global-btn"
            >
              See Full Report <RightArrow/>
            </div>
            </div>
          )}
        </>
      )}
      {/* </div> */}
      {EngagementModalShow && (
        <LandingPageModal
          data={EngagementData}
          show={EngagementModalShow}
          onHide={() => setEngagementModalShow(false)}
        />
      )}
    </>
  );
};

export default LandingPageTable;
