import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  dateFilter: localStorage.getItem("dateFilter")
    ? JSON.parse(localStorage.getItem("dateFilter"))
    : null,
};

const DateFilterSlice = createSlice({
  name: "datefilter",
  initialState,
  reducers: {
    dateFilter: (state, action) => {
      state.dateFilter = action.payload;
      localStorage.setItem("dateFilter", JSON.stringify(action.payload));
    },
  },
});

export const { dateFilter } = DateFilterSlice.actions;
export default DateFilterSlice.reducer;
