import React from "react";
import NoDataImg from "../Assets/nodata1.png";
const NoData = (props) => {
  return (
    <>
      {props.type === "table" ? (
        <tr>
          <td colSpan="12" className="no-data-container">
            <img src={NoDataImg} alt="No Data" className="no-data-img" />
          </td>
        </tr>
      ) : (
        <div className="no-data-container">
          <img src={NoDataImg} alt="No Data" className="no-data-img" />
        </div>
      )}
    </>
  );
};

export default NoData;
