import React, { useEffect, useState } from "react";
import AreaChartComponent from "./AreaChartComponent";
import moment from "moment";

const ActiveUserMainTabChart = (props) => {
  const [ActiveUserData, setActiveUserData] = useState({
    labels: [],
    datasets: [],
  });
  useEffect(() => {
    let tempObj = {};
    props.data.forEach((element) => {
      const formattedDate = moment(element.dimension, "YYYYMMDD").format(
        "DD-MM-YYYY"
      );
      if (tempObj[formattedDate]) {
        tempObj[formattedDate] = {
          ...tempObj[formattedDate],
          value:
            tempObj[formattedDate].value +
            parseInt(element.metrics.activeUsers, 10),
        };
      } else {
        tempObj[formattedDate] = {
          date: formattedDate,
          title: "activeUsers",
          value: parseInt(element.metrics.activeUsers, 10),
        };
      }
    });
    const labels = Object.keys(tempObj);
        const counts = labels.map((t) => tempObj[t]?.value);
        const datasets = [
          {
            fill: true,
            data: counts,
            backgroundColor:'rgba(48,63,159,0.3)',
            tension: 0.4,
            
          //   backgroundColor: labels.map(
          //     () =>
          //       `rgba(${Math.floor(Math.random() * 256)}, ${Math.floor(
          //         Math.random() * 256
          //       )}, ${Math.floor(Math.random() * 256)}, 0.5)`
          //   ),
          },
        ];
        // console.log("datasets ::", datasets);
        setActiveUserData({
          labels,
          datasets,
        });
  }, [props]);

  return (
    <div>
      <AreaChartComponent data={ActiveUserData}/>
    </div>
  );
};

export default ActiveUserMainTabChart;
