import React, { useEffect, useState } from "react";
import { CommonServices } from "../Common/CommonService";
import axios from "axios";
import { useSelector } from "react-redux";
import { Table } from "react-bootstrap";
import CountryModal from "../Modals/CountryModal";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";
import RightArrow from "../Assets/RightArrow";

const CountryTab = (props) => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [CountryDataLoader, setCountryDataLoader] = useState(false);
  const [CountryModalShow, setCountryModalShow] = useState(false);
  const [CountryData, setCountryData] = useState([]);
  async function getCountryReport() {
    setCountryDataLoader(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + props.apiname,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse,setCountryDataLoader);
    if (response?.isValidate) {
      setCountryData(response.data);
      setCountryDataLoader(false);
    }
    if (!response?.isValidate) {
      
    }
    // try {
    //   setCountryDataLoader(true);
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   axios
    //     .get(`http://localhost:8000/api/${props.apiname}`, { params: body })
    //     .then((response) => {
    //       setCountryData(response.data);
    //       setCountryDataLoader(false);
    //     });
    // } catch (error) {
    //   toast.error(error?.response?.data?.message + `in ${props.apiname}.`);
    // }
  }
  useEffect(() => {
    if (DateFilterValue) {
      getCountryReport();
    }
  }, [DateFilterValue]);

  CommonServices.setBackColorPerByTDValue(
    "#1a73e8",
    "Red",
    `#tableTdBGcolr${props.title} .perbackground-otb`
  );
  return (
    <>
      {CountryDataLoader ? (
        <LoadingComp />
      ) : (
        <>
          <Table id={`tableTdBGcolr${props.title}`} striped hover>
            <thead>
              <tr>
                <th>{props?.title}</th>
                <th>Visitors</th>
              </tr>
            </thead>
            <tbody>
              {CountryData?.length > 0
                ? CountryData?.map(
                    (item_e, index_e) =>
                      index_e < 5 && (
                        <tr key={index_e}>
                          <td
                            value={item_e?.value ? item_e?.value : 0}
                            className="perbackground perbackground-otb"
                          >
                            {item_e?.deviceName}
                          </td>
                          <td>
                            {CommonServices.NumberCalculateValue(item_e?.value)}
                          </td>
                        </tr>
                      )
                  )
                : "No data"}
            </tbody>
          </Table>
          {CountryData?.length > 5 && (
            <div className="text-end">
            <div
              onClick={() => setCountryModalShow(true)}
              className="blue-text global-btn"
            >
               See Full Report <RightArrow/>
            </div>
            </div>
          )}
        </>
      )}
      {CountryModalShow && (
        <CountryModal
          title={props?.title}
          data={CountryData}
          show={CountryModalShow}
          onHide={() => setCountryModalShow(false)}
        />
      )}
    </>
  );
};

export default CountryTab;
