import axios from "axios";
import React, { useEffect, useState } from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import { useSelector } from "react-redux";
import { CommonServices } from "../Common/CommonService";
import ActiveUserMainTabChart from "./ActiveUserMainTabChart";
import NewUserMainTabChart from "./NewUserMainTabChart";
import SessionMainTabChart from "./SessionMainTabChart";
import TotalEventMainTabChart from "./TotalEventMainTabChart";
import TotalRevenueMainTabChart from "./TotalRevenueMainTabChart";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import Boxheading from "../Common/Boxheading";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";

const MainTabCharts = () => {
  const [UserData, setUserData] = useState([]);
  const [Loading, setLoading] = useState(false);
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [reportData, setReportData] = useState({
    activeUsers: 0,
    newUsers: 0,
    sessions: 0,
    userEngagementDuration: 0,
    eventCount: 0,
    totalRevenue: 0,
  });

  const getEventsReport = async () => {
    setLoading(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.GENERALTABREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse, setLoading);
    if (response?.isValidate) {
      setUserData(response?.data);
      let totalActiveUsers = 0;
      let totalnewUsers = 0;
      let totalsessions = 0;
      let totaluserEngagementDuration = 0;
      let totalusereventCount = 0;
      let totalRevenue = 0;
      response.data.forEach((element) => {
        totalActiveUsers += parseInt(element.metrics.activeUsers, 10);
        totalnewUsers += parseInt(element.metrics.newUsers, 10);
        totalsessions += parseInt(element.metrics.sessions, 10);
        totaluserEngagementDuration += parseInt(
          element.metrics.userEngagementDuration,
          10
        );
        totalusereventCount += parseInt(element.metrics.eventCount, 10);
        totalRevenue += parseInt(element.metrics.totalRevenue, 10);
      });
      setReportData({
        activeUsers: totalActiveUsers,
        newUsers: totalnewUsers,
        sessions: totalsessions,
        userEngagementDuration: totaluserEngagementDuration,
        eventCount: totalusereventCount,
        totalRevenue: totalRevenue,
      });
    }
    if (!response?.isValidate) {
    }
    // try {
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   const response = await axios.get(
    //     `http://localhost:8000/api/generaltabreport`,
    //     { params: body }
    //   );
    //   if (response?.data) {
    //     setLoading(false);
    //     setUserData(response?.data);
    //     let totalActiveUsers = 0;
    //     let totalnewUsers = 0;
    //     let totalsessions = 0;
    //     let totaluserEngagementDuration = 0;
    //     let totalusereventCount = 0;
    //     let totalRevenue = 0;
    //     response.data.forEach((element) => {
    //       totalActiveUsers += parseInt(element.metrics.activeUsers, 10);
    //       totalnewUsers += parseInt(element.metrics.newUsers, 10);
    //       totalsessions += parseInt(element.metrics.sessions, 10);
    //       totaluserEngagementDuration += parseInt(
    //         element.metrics.userEngagementDuration,
    //         10
    //       );
    //       totalusereventCount += parseInt(element.metrics.eventCount, 10);
    //       totalRevenue += parseInt(element.metrics.totalRevenue, 10);
    //     });
    //     setReportData({
    //       activeUsers: totalActiveUsers,
    //       newUsers: totalnewUsers,
    //       sessions: totalsessions,
    //       userEngagementDuration: totaluserEngagementDuration,
    //       eventCount: totalusereventCount,
    //       totalRevenue: totalRevenue,
    //     });
    //   }
    // } catch (error) {
    //   setLoading(false);
    //   toast.error(error?.response?.data?.message + "in generaltabreport");
    // }
  };

  useEffect(() => {
    if (DateFilterValue) {
      getEventsReport();
    }
  }, [DateFilterValue]);
  return (
    <div className="box-shadow first-table">
      <Boxheading title="Reports Snapshot" />
      <Tabs
        defaultActiveKey="activeUsers"
        id="uncontrolled-tab-example"
        // className="mb-3"
        fill
      >
        <Tab
          eventKey="activeUsers"
          title={
            <>
              <div>
                <p className="m-0">Users</p>
                <h4>
                  <strong>
                    {CommonServices.NumberCalculateValue(
                      reportData.activeUsers
                    )}
                  </strong>
                </h4>
              </div>
            </>
          }
        >
          {Loading ? (
            <LoadingComp />
          ) : (
            <ActiveUserMainTabChart data={UserData} />
          )}
        </Tab>
        <Tab
          eventKey="newUsers"
          title={
            <>
              <div>
                <p className="m-0">New users</p>
                <h4>
                  <strong>
                    {CommonServices.NumberCalculateValue(reportData.newUsers)}
                  </strong>
                </h4>
              </div>
            </>
          }
        >
          {Loading ? <LoadingComp /> : <NewUserMainTabChart data={UserData} />}
        </Tab>
        <Tab
          eventKey="sessions"
          title={
            <>
              <div>
                <p className="m-0">Sessions</p>
                <h4>
                  <strong>
                    {CommonServices.NumberCalculateValue(reportData.sessions)}
                  </strong>
                </h4>
              </div>
            </>
          }
        >
          {Loading ? <LoadingComp /> : <SessionMainTabChart data={UserData} />}
        </Tab>
        <Tab
          eventKey="eventCount"
          title={
            <>
              <div>
                <p className="m-0">Total event count</p>
                <h4>
                  <strong>
                    {CommonServices.NumberCalculateValue(reportData.eventCount)}
                  </strong>
                </h4>
              </div>
            </>
          }
        >
          {Loading ? (
            <LoadingComp />
          ) : (
            <TotalEventMainTabChart data={UserData} />
          )}
        </Tab>
        {/* <Tab
          eventKey="totalRevenue"
          title={
            <>
              <div>
                <p className="m-0">Total revenue</p>
                <h4>
                  <strong>
                    $
                    {CommonServices.NumberCalculateValue(
                      reportData.totalRevenue
                    )}
                  </strong>
                </h4>
              </div>
            </>
          }
        >
          {Loading ? (
            <LoadingComp />
          ) : (
            <TotalRevenueMainTabChart data={UserData} />
          )}
        </Tab> */}
      </Tabs>
    </div>
  );
};

export default MainTabCharts;
