import React, { useEffect, useState } from "react";
import DoughnutChart from "./DoughnutChart";
import { useSelector } from "react-redux";
import { CommonServices } from "../Common/CommonService";
import axios from "axios";
import Boxheading from "../Common/Boxheading";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";

const BrowserChart = () => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [EventCountNameData, setEventCountNameData] = useState({
    labels: [],
    datasets: [],
  });
  const [Loading, setLoading] = useState(false);
  const getEventsReport = async () => {
    setLoading(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.BROWSERREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse, setLoading);
    if (response?.isValidate) {
      let eventnameObj = {};
      response.data.forEach((element) => {
        const count = element?.value ? parseInt(element.value) : 0;
        if (eventnameObj[element.deviceName]) {
          eventnameObj[element.deviceName].count += count;
        } else {
          eventnameObj[element.deviceName] = { count };
        }
      });
      const labels = Object.keys(eventnameObj);
      const counts = labels.map((labelItem) => eventnameObj[labelItem].count);
      const datasets = [
        {
          // label: labels,
          data: counts,
          backgroundColor: labels.map(
            () =>
              `rgba(${Math.floor(Math.random() * 256)}, ${Math.floor(
                Math.random() * 256
              )}, ${Math.floor(Math.random() * 256)}, 0.5)`
          ),
        },
      ];

      setEventCountNameData({
        labels,
        datasets,
      });
    }
    if (!response?.isValidate) {
      
      setEventCountNameData({
        labels: [],
        datasets: [],
      });
    }
    // try {
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   const response = await axios.get(
    //     `http://localhost:8000/api/browserreport`,
    //     { params: body }
    //   );
    //   if (response?.data) {
    //     setLoading(false);
    //     let eventnameObj = {};
    //     response.data.forEach((element) => {
    //       const count = element?.value ? parseInt(element.value) : 0;
    //       if (eventnameObj[element.deviceName]) {
    //         eventnameObj[element.deviceName].count += count;
    //       } else {
    //         eventnameObj[element.deviceName] = { count };
    //       }
    //     });

    //     const labels = Object.keys(eventnameObj);
    //     const counts = labels.map((labelItem) => eventnameObj[labelItem].count);

    //     const datasets = [
    //       {
    //         // label: labels,
    //         data: counts,
    //         backgroundColor: labels.map(
    //           () =>
    //             `rgba(${Math.floor(Math.random() * 256)}, ${Math.floor(
    //               Math.random() * 256
    //             )}, ${Math.floor(Math.random() * 256)}, 0.5)`
    //         ),
    //         // borderColor: labels.map(
    //         //   () =>
    //         //     `rgba(${Math.floor(Math.random() * 256)}, ${Math.floor(
    //         //       Math.random() * 256
    //         //     )}, ${Math.floor(Math.random() * 256)}, 1)`
    //         // ),
    //         // borderWidth: 1,
    //       },
    //     ];

    //     setEventCountNameData({
    //       labels,
    //       datasets,
    //     });
    //   } else {
    //     setLoading(false);
    //     setEventCountNameData({
    //       labels: [],
    //       datasets: [],
    //     });
    //   }
    // } catch (error) {
    //   setLoading(false);
    //   toast.error(error?.response?.data?.message + "in browserreport.");
    //   setEventCountNameData({
    //     labels: [],
    //     datasets: [],
    //   });
    // }
  };

  useEffect(() => {
    if (DateFilterValue) {
      getEventsReport();
    }
  }, [DateFilterValue]);
  return (
    <div className="box-shadow-Doughnut">
      <Boxheading title="Browser Chart" />
      {Loading ? <LoadingComp /> : <DoughnutChart data={EventCountNameData} />}
    </div>
  );
};

export default BrowserChart;
