import React, { useState } from "react";
import { Col, FloatingLabel, Form, Modal, Row, Table } from "react-bootstrap";
import { CommonServices } from "../Common/CommonService";
import NoData from "../Components/NoData";

function EngagementTableModal(props) {
  return (
    <Modal className="scheduleCheckListModal" size="xl" centered {...props}>
      <Modal.Header closeButton>
        <Modal.Title>Engagement Table</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table striped hover responsive>
          <thead>
            <tr>
              <th>Page</th>
              <th>Visits/Unique visitors</th>
              <th>Visit duration</th>
              <th>Page per visits</th>
              <th>Bounce rate</th>
              <th>Page Views</th>
              <th>Total Revenue</th>
            </tr>
          </thead>
          <tbody>
            {props?.data?.length > 0
              ? props?.data?.map((item_e, index_e) => (
                  <tr key={index_e}>
                    <td>{item_e?.dimension}</td>
                    <td>{CommonServices.NumberCalculateValue(item_e?.metrics?.activeUsers)}</td>
                    <td>
                      {CommonServices.TimestampConverter(
                        item_e?.metrics?.averageSessionDuration
                      )}
                    </td>
                    <td>{CommonServices.NumberCalculateValue(item_e?.metrics?.newUsers)}</td>
                    <td>
                      {parseFloat(item_e?.metrics?.engagementRate)?.toFixed(2)}%
                    </td>
                    <td>
                      {parseFloat(item_e?.metrics?.pagesPerVisit)?.toFixed(2)}
                    </td>
                    <td>{item_e?.metrics?.totalRevenue}</td>
                  </tr>
                ))
              : <NoData type='table'/>}
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default EngagementTableModal;
