import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import CountryTab from "./CountryTab";
import Boxheading from "../Common/Boxheading";

const LocationTab = () => {
  return (
    <>
      <div className="box-shadow-height global-tabbing ">
        <Boxheading title="Locations" />
        <Tabs
          defaultActiveKey="City"
          id="uncontrolled-tab-example"
          // className="mb-3"
        >
          <Tab eventKey="City" title="City">
            <CountryTab apiname="cityreport" title="City" />
          </Tab>
          <Tab eventKey="Countries" title="Countries">
            <CountryTab apiname="countryreport" title="Country" />
          </Tab>
          <Tab eventKey="Regions" title="Regions">
            <CountryTab apiname="regionreport" title="Regions" />
          </Tab>
          
        </Tabs>
      </div>
    </>
  );
};

export default LocationTab;
