import React, { useEffect, useRef, useState } from "react";
import Form from "react-bootstrap/Form";
import DatePicker, { getAllDatesInRange } from "react-multi-date-picker";
import DatePanel from "react-multi-date-picker/plugins/date_panel";
import { useDispatch, useSelector } from "react-redux";
import ENUM from "../Common/ENUM";
import { dateFilter } from "../ReduxToolkit/DateFilterSlice";
import { CommonServices } from "../Common/CommonService";
import axios from "axios";
import logo1 from "../Logo_Google_Analytics.svg.png";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";
import moment from "moment";

const Header = () => {
  const datePickerRef = useRef(null);
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  let dispatch = useDispatch();
  const [DatePickShow, setDatePickShow] = useState(false);
  const [dates, setDates] = useState([
    moment(DateFilterValue?.startdate).format("YYYY/MM/DD"),
    moment(DateFilterValue?.enddate).format("YYYY/MM/DD"),
  ]);
  const [allDates, setAllDates] = useState([]);
  const [RealTimeLoader, setRealTimeLoader] = useState(false);
  const [RealTimrReport, setRealTimrReport] = useState(0);
  const [StartEndDate, setStartEndDate] = useState({
    startdate: CommonServices.dateRangesFormat(
      new Date(new Date().getTime() - 30 * 24 * 60 * 60 * 1000)
    ),
    enddate: CommonServices.dateRangesFormat(new Date()),
  });

  function HandleDateChangeFilter(event) {
    setDatePickShow(false);

    if (event === "Last 7 Days") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 7 * 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "Last 28 Days") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 28 * 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "All time") {
      setStartEndDate({
        startdate: new Date("2015-10-01"),
        enddate: new Date(),
      });
    } else if (event === "Last 30 Days") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 30 * 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "Last 90 Days") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 90 * 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "Last 12 Months") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 365 * 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "Yesterday") {
      setStartEndDate({
        startdate: new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
        enddate: new Date(),
      });
    } else if (event === "Today") {
      setStartEndDate({
        startdate: new Date(),
        enddate: new Date(),
      });
    } else if (event === "Last Calendar Year") {
      const today = new Date();
      const year = today.getFullYear();
      setStartEndDate({
        startdate: new Date(year - 1, 0, 1),
        enddate: new Date(year, 0, 1),
      });
    } else if (event === "This Year (January - Today)") {
      const today = new Date();
      const year = today.getFullYear();
      setStartEndDate({
        startdate: new Date(year, 0, 1),
        enddate: new Date(),
      });
    } else if (event === "Custom") {
      setDatePickShow(true);
    }
  }

  useEffect(() => {
    dispatch(dateFilter(StartEndDate));
    // getRealReport();

    // const interval = setInterval(() => {
    getRealReport();
    // }, 20000);

    // Clear the interval on component unmount
    // return () => clearInterval(interval);
    // setDates(StartEndDate.startdate, StartEndDate.enddate);
  }, [StartEndDate]);

  async function getRealReport() {
    setRealTimeLoader(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue?.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue?.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.REALTIMEREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse, setRealTimeLoader);
    if (response?.isValidate) {
      setRealTimrReport(response?.data?.rows[0]?.metricValues[0]?.value);
    }
    if (!response?.isValidate) {
    }
    // axios
    //   .get(`http://localhost:8000/api/realtimereport`, { params: body })
    //   .then((response) => {
    //     setRealTimrReport(response?.data?.rows[0]?.metricValues[0]?.value);
    //     setRealTimeLoader(false);
    //   });
  }

  return (
    <div className=" justify-content-between align-items-center py-4 header-heading">
      <div className="d-flex align-items-center">
        <img src={logo1} height={50} />
        {/* <h2 className="m-0">Google Analytic</h2> */}
        {RealTimrReport > 0 && (
          <p className="m-0 ms-5 grren-dot">
            {RealTimrReport} current visitors
          </p>
        )}
      </div>
      <div>
        <Form.Select
          onChange={(e) => HandleDateChangeFilter(e.target.value)}
          aria-label="Default select example"
        >
          {ENUM.DateFilterArray.map((itemd, indexd) => (
            <option key={indexd} value={itemd}>
              {itemd}
            </option>
          ))}
        </Form.Select>
        {DatePickShow && (
          <div>
            <DatePicker
              range
              placeholder={
                moment(DateFilterValue.startdate).format("YYYY/MM/DD") +
                "~" +
                moment(DateFilterValue.enddate).format("YYYY/MM/DD")
              }
              calendarPosition="top-left"
              value={dates}
              maxDate={new Date()}
              onChange={(dateObjects) => {
                setDates(dateObjects);
                setAllDates(getAllDatesInRange(dateObjects));
                if (dateObjects[0] && dateObjects[1]) {
                  setStartEndDate({
                    startdate: new Date(dateObjects[0]),
                    enddate: new Date(dateObjects[1]),
                  });
                }
              }}
              plugins={[<DatePanel eachDaysInRange />]}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default Header;
