import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CommonServices } from "../Common/CommonService";
import axios from "axios";
import { Table } from "react-bootstrap";
import Boxheading from "../Common/Boxheading";
import NoData from "./NoData";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { API_URL } from "../Common/APIRoutes";
import { apiCall } from "../Common/apiCall";
import { apiResponse } from "../Common/apiResponse";

const OrganicSearchTable = () => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [OrganicTableDataLoader, setOrganicTableDataLoader] = useState(false);
  const [EngagementModalShow, setEngagementModalShow] = useState(false);
  const [OrganicTableData, setOrganicTableData] = useState([]);
  async function getEngagementReport() {
    setOrganicTableDataLoader(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.ORGANICSEARCHTABLEREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(
      true,
      heatMapResponse,
      setOrganicTableDataLoader
    );
    if (response?.isValidate) {
      setOrganicTableData(response?.data?.resultArray);
    }
    if (!response?.isValidate) {
    }
    // try {
    //   setOrganicTableDataLoader(true);
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   axios
    //     .get(`http://localhost:8000/api/organicsearchtablereport`, {
    //       params: body,
    //     })
    //     .then((response) => {
    //       setOrganicTableData(response?.data?.resultArray);
    //       setOrganicTableDataLoader(false);
    //     });
    // } catch (error) {
    //   setOrganicTableDataLoader(false);
    //   toast.error(
    //     error?.response?.data?.message + "in organicsearchtablereport."
    //   );
    // }
  }
  useEffect(() => {
    if (DateFilterValue) {
      getEngagementReport();
    }
  }, [DateFilterValue]);

  return (
    <div className="box-shadow">
      <Boxheading title="All channels Overview" />
      {OrganicTableDataLoader ? (
        <LoadingComp />
      ) : (
        <Table striped hover responsive>
          <thead>
            <tr>
              <th>Channel Group</th>
              <th>Users</th>
              <th>Sessions</th>
              <th>Engaged sessions</th>
              <th>Average Engagement time per session</th>
              <th>Engaged sessions per user</th>
              <th>Event per session</th>
              <th>Engagement rate</th>
              <th>Event counts</th>
              <th>Key Events</th>
              <th>Total Revenue</th>
            </tr>
          </thead>
          <tbody>
            {OrganicTableData?.length > 0 ? (
              OrganicTableData?.map((itemL, indexL) => (
                <tr key={indexL}>
                  <td>{itemL?.dimension}</td>
                  <td>
                    {CommonServices.NumberCalculateValue(
                      itemL.metrics.activeUsers
                    )}
                  </td>
                  <td>
                    {CommonServices.NumberCalculateValue(
                      itemL.metrics.sessions
                    )}
                  </td>
                  <td>
                    {CommonServices.NumberCalculateValue(
                      itemL.metrics.engagedSessions
                    )}
                  </td>
                  <td>
                    {CommonServices.TimestampConverter(
                      itemL.metrics.averageEngagementTimePerSession
                    )}
                  </td>
                  <td>
                    {parseFloat(itemL.metrics.engagedSessionsPerUser).toFixed(
                      2
                    )}
                  </td>
                  <td>
                    {parseFloat(itemL.metrics.eventsPerSession).toFixed(2)}
                  </td>
                  <td>
                    {parseFloat(itemL.metrics.engagementRate).toFixed(2)}%
                  </td>
                  <td>
                    {CommonServices.NumberCalculateValue(
                      itemL.metrics.eventCount
                    )}
                  </td>
                  <td>
                    {CommonServices.NumberCalculateValue(
                      itemL.metrics.conversions
                    )}
                  </td>
                  <td>${parseFloat(itemL.metrics.totalRevenue).toFixed(2)}</td>
                </tr>
              ))
            ) : (
              <NoData type="table" />
            )}
          </tbody>
        </Table>
      )}
    </div>
  );
};

export default OrganicSearchTable;
