import React, { useState } from "react";
import { Col, FloatingLabel, Form, Modal, Row, Table } from "react-bootstrap";
import { CommonServices } from "../Common/CommonService";
import NoData from "../Components/NoData";

function ReferingWebModal(props) {

  
  return (
    <Modal className="scheduleCheckListModal" size="xl" centered {...props}>
      <Modal.Header closeButton>
        <Modal.Title>Top Referring Wesites</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table striped hover responsive>
          <thead>
            <tr>
              <th>Domain</th>
              <th>Website Categories</th>
              <th>Visitors</th>
            </tr>
          </thead>
          <tbody>
            {props?.data?.length > 0
              ? props?.data?.reverse()?.map((itemL, indexL) => (
                  <tr key={indexL}>
                    <td>{itemL?.source}</td>
                    <td>{itemL?.sourceCategory}</td>
                    <td>{itemL?.value}</td>
                  </tr>
                ))
              : <NoData type='table'/>}
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default ReferingWebModal;
