import React, { useState } from "react";
import { Col, FloatingLabel, Form, Modal, Row, Table } from "react-bootstrap";
import { CommonServices } from "../Common/CommonService";
import NoData from "../Components/NoData";

function OrganicSearchModal(props) {
  return (
    <Modal className="scheduleCheckListModal" size="xl" centered {...props}>
      <Modal.Header closeButton>
        <Modal.Title>{props.title}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table striped hover responsive>
          <thead>
            <tr>
              <th>Landing Page</th>
              <th>Active Users</th>
              <th>Sessions</th>
              <th>Key Events</th>
              <th>Engagemen Rate</th>
              <th>Total Revenue</th>
            </tr>
          </thead>
          <tbody>
            {props?.data?.length > 0
              ? props?.data?.map((itemL, indexL) => (
                  <tr key={indexL}>
                    <td>{itemL?.landingPage}</td>
                    <td>{itemL?.activeUsers}</td>
                    <td>{itemL?.engagedSession}</td>
                    <td>{parseFloat(itemL?.keyevents).toFixed(2)}</td>
                    <td>{parseFloat(itemL?.engagementRate).toFixed(2)}%</td>
                    <td>${parseFloat(itemL?.Revenue).toFixed(2)}</td>
                  </tr>
                ))
              : <NoData type='table'/>}
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default OrganicSearchModal;
