import axios from "axios";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CommonServices } from "../Common/CommonService";
import {
  BarElement,
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  Title,
  Tooltip,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import Boxheading from "../Common/Boxheading";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);
const options1 = {
  indexAxis: "y",
  responsive: true,
  plugins: {
    legend: {
      position: "right",
      display: false,
    },
    title: {
      display: false,
      text: "Horizontal Bar Chart",
    },
  },
};

const MarkettingChannelChart = () => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [ChannelChartData, setChannelChartData] = useState({
    labels: [],
    datasets: [],
  });
  const [ChannelChartDataLoader, setChannelChartDataLoader] = useState(false);

  async function getMarketingChannelsChartData() {
    setChannelChartDataLoader(false);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.MARKETTINGCHANNELREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(
      true,
      heatMapResponse,
      setChannelChartDataLoader
    );
    if (response?.isValidate) {
      let titlearray = [];
      let countarray = [];
      if (response?.data) {
        response?.data.forEach((element) => {
          titlearray.push(element.Title);
          countarray.push(element.value);
        });
      }
      const datasets = [
        {
          label: "All Channel Overview",
          data: countarray,
          backgroundColor: "#1a73e8",
        },
      ];
      setChannelChartData({
        labels: titlearray,
        datasets: datasets,
      });
      // setChannelChartDataLoader(false);
      // setCountryData(response.data);
      // setCountryDataLoader(false);
    }
    if (!response?.isValidate) {
    }
    // try {
    //   setChannelChartDataLoader(true);
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   axios
    //     .get(`http://localhost:8000/api/markettingchannelreport`, {
    //       params: body,
    //     })
    //     .then((response) => {
    //       let titlearray = [];
    //       let countarray = [];
    //       if (response?.data) {
    //         response?.data.forEach((element) => {
    //           titlearray.push(element.Title);
    //           countarray.push(element.value);
    //         });
    //       }
    //       const datasets = [
    //         {
    //           label: "All Channel Overview",
    //           data: countarray,
    //           backgroundColor: "rgba(48,63,159,0.3)",
    //         },
    //       ];
    //       setChannelChartData({
    //         labels: titlearray,
    //         datasets: datasets,
    //       });
    //       setChannelChartDataLoader(false);
    //     });
    // } catch (error) {
    //   setChannelChartDataLoader(false);
    //   toast.error(
    //     error?.response?.data?.message + "in markettingchannelreport."
    //   );
    // }
  }
  useEffect(() => {
    if (DateFilterValue) {
      getMarketingChannelsChartData();
    }
  }, [DateFilterValue]);
  return (
    <>
      <div className="box-shadow">
        <Boxheading title="All channels" />
        {ChannelChartDataLoader ? (
          <LoadingComp />
        ) : (
          <Bar data={ChannelChartData} options={options1} />
        )}
      </div>
    </>
  );
};

export default MarkettingChannelChart;
