    //   console.log("response :: ", response);

    //   let api = apiResponse(false, response);

    //   if (api?.isValidate) {
    //     setLoading(false);
    //     let eventnameObj = {};
    //     response.data.forEach((element) => {
    //       const count = element?.value ? parseInt(element.value) : 0;
    //       if (eventnameObj[element.Title]) {
    //         eventnameObj[element.Title].count += count;
    //       } else {
    //         eventnameObj[element.Title] = { count };
    //       }
    //     });

    //     const labels = Object.keys(eventnameObj);
    //     const counts = labels.map((labelItem) => eventnameObj[labelItem].count);

    //     const datasets = [
    //       {
    //         // fill: true,
    //         // label: "All Events count",
    //         data: counts,
    //         backgroundColor: "rgba(48,63,159,0.3)",
    //         // backgroundColor: labels.map(
    //         //   () =>
    //         //     `rgba(${Math.floor(Math.random() * 256)}, ${Math.floor(
    //         //       Math.random() * 256
    //         //     )}, ${Math.floor(Math.random() * 256)}, 0.5)`
    //         // ),
    //       },
    //     ];

    //     setEventCountNameData({
    //       labels,
    //       datasets,
    //     });
    //   } else {
    //     setEventCountNameData({
    //       labels: [],
    //       datasets: [],
    //     });
    //     setLoading(false);
    //   }
    //   // if (response?.data) {
