import React, { useState } from "react";
import { Col, FloatingLabel, Form, Modal, Row, Table } from "react-bootstrap";
import { CommonServices } from "../Common/CommonService";
import NoData from "../Components/NoData";
// import { CommonServices } from "../../GAfrontend/src/Common/CommonService";

function LandingPageModal(props) {
  return (
    <Modal className="scheduleCheckListModal" size="xl" centered {...props}>
      <Modal.Header closeButton>
        <Modal.Title>Landing page</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table striped hover responsive>
          <thead>
            <tr>
              <th>Landing page</th>
              <th>Sessions</th>
              <th>Users</th>
              <th>New Users</th>
              <th>Average Engagement time per session</th>
              <th>Key Events</th>
              <th>Total Revenue</th>
            </tr>
          </thead>
          <tbody>
            {props?.data?.length > 0
              ? props?.data?.map((item_e, index_e) => (
                  <tr key={index_e}>
                    <td>{item_e?.dimension}</td>
                    <td>
                      {CommonServices.NumberCalculateValue(
                        item_e.metrics.sessions
                      )}
                    </td>
                    <td>
                      {CommonServices.NumberCalculateValue(
                        item_e.metrics.activeUsers
                      )}
                    </td>
                    <td>
                      {CommonServices.NumberCalculateValue(
                        item_e.metrics.newUsers
                      )}
                    </td>
                    <td>
                      {CommonServices.TimestampConverter(
                        item_e.metrics.averageEngagementTimePerSession
                      )}
                    </td>
                    <td>
                      {CommonServices.NumberCalculateValue(
                        item_e.metrics.keyevents
                      )}
                    </td>
                    <td>
                      ${parseFloat(item_e.metrics.totalRevenue).toFixed(2)}
                    </td>
                  </tr>
                ))
              : <NoData type='table'/>}
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default LandingPageModal;
