import React, { useState } from "react";
import { Col, FloatingLabel, Form, Modal, Row, Table } from "react-bootstrap";
import { CommonServices } from "../Common/CommonService";
import NoData from "../Components/NoData";

function CountryModal(props) {
  CommonServices.setBackColorPerByTDValue(
    "#1a73e8",
    "Red",
    `#tableTdBGcolr .perbackground-otb`
  );
  return (
    <Modal className="scheduleCheckListModal" size="xl" centered {...props}>
      <Modal.Header closeButton>
        <Modal.Title>{props?.title}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table id="tableTdBGcolr" striped bordered hover>
          <thead>
            <tr>
              <th>{props?.title}</th>
              <th>Visitors</th>
            </tr>
          </thead>
          <tbody>
            {props?.data?.length > 0
              ? props?.data?.map((item_e, index_e) => (
                  <tr key={index_e}>
                    <td
                      value={item_e?.value ? item_e?.value : 0}
                      className="perbackground perbackground-otb"
                    >
                      {item_e?.deviceName}
                    </td>
                    <td>{CommonServices.NumberCalculateValue(item_e?.value)}</td>
                  </tr>
                ))
              : <NoData type='table'/>}
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default CountryModal;
