import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import EngagementTable from "./EngagementTable";
import LandingPageTable from "./LandingPageTable";
import Boxheading from "../Common/Boxheading";

const PagesScreenTab = () => {
  return (
    <>
      <div className="box-shadow global-tabbing ">
        <Boxheading title="Engagement" />
        <Tabs
          defaultActiveKey="PageTitle"
          id="uncontrolled-tab-example"
          // className="mb-3"
        >
          <Tab
            eventKey="PageTitle"
            title="Views by Page title and screen class"
          >
            <EngagementTable apiname='engagementtablereport' />
          </Tab>
          <Tab eventKey="PagePath" title="Views by Page Path">
            <EngagementTable apiname='engagementpagepathreport' />
          </Tab>
          <Tab eventKey="LandingPage" title="Landing Page">
            <LandingPageTable />
          </Tab>
         
        </Tabs>
      </div>
    </>
  );
};

export default PagesScreenTab;
