import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Line } from "react-chartjs-2";
import axios from "axios";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend,
} from "chart.js";
import { CommonServices } from "../Common/CommonService";
import Boxheading from "../Common/Boxheading";
import { toast } from "react-toastify";
import LoadingComp from "./LoadingComp";
import { apiCall } from "../Common/apiCall";
import { API_URL } from "../Common/APIRoutes";
import { apiResponse } from "../Common/apiResponse";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend
);

export const options = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
      position: "bottom",
    },
    title: {
      display: false,
      text: "Chart.js Line Chart",
    },
  },
};

const TrafficTabChart = () => {
  const DateFilterValue = useSelector(
    (state) => state.DateFilterInfo.dateFilter
  );
  const [EventCountNameData, setEventCountNameData] = useState({
    labels: [],
    datasets: [],
  });
  const [Loading, setLoading] = useState(false);
  const getEventsReport = async () => {
    setLoading(true);
    const body = {
      startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
      endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    };
    let heatMapResponse = await apiCall(
      {
        method: "GET",
        url: API_URL.BASEURL + API_URL.EVENTNAMECHARTREPORT,
        body: body,
      },
      false
    );
    let response = apiResponse(true, heatMapResponse, setLoading);
    if (response?.isValidate) {
      let eventnameObj = {};
      response.data.forEach((element) => {
        const count = element?.eventCount ? parseInt(element.eventCount) : 0;
        if (eventnameObj[element.eventName]) {
          eventnameObj[element.eventName].count += count;
        } else {
          eventnameObj[element.eventName] = { count };
        }
      });

      const labels = Object.keys(eventnameObj);
      const counts = labels.map((labelItem) => eventnameObj[labelItem].count);

      const datasets = [
        {
          // fill: true,
          label: "All Events count",
          data: counts,
          borderColor: "#1a73e8",
        },
      ];

      setEventCountNameData({
        labels,
        datasets,
      });
    }
    if (!response?.isValidate) {
      setEventCountNameData({
        labels: [],
        datasets: [],
      });
    }
    // try {
    //   const body = {
    //     startDate: CommonServices.dateRangesFormat(DateFilterValue.startdate),
    //     endDate: CommonServices.dateRangesFormat(DateFilterValue.enddate),
    //   };
    //   const response = await axios.get(
    //     `http://localhost:8000/api/eventnamechartreport`,
    //     { params: body }
    //   );

    //   if (response?.data) {
    //     setLoading(false);
    //     let eventnameObj = {};
    //     response.data.forEach((element) => {
    //       const count = element?.eventCount ? parseInt(element.eventCount) : 0;
    //       if (eventnameObj[element.eventName]) {
    //         eventnameObj[element.eventName].count += count;
    //       } else {
    //         eventnameObj[element.eventName] = { count };
    //       }
    //     });

    //     const labels = Object.keys(eventnameObj);
    //     const counts = labels.map((labelItem) => eventnameObj[labelItem].count);

    //     const datasets = [
    //       {
    //         // fill: true,
    //         label: "All Events count",
    //         data: counts,
    //         borderColor: "rgba(48,63,159,0.3)",
    //       },
    //     ];

    //     setEventCountNameData({
    //       labels,
    //       datasets,
    //     });
    //   } else {
    //     setLoading(false);
    //     setEventCountNameData({
    //       labels: [],
    //       datasets: [],
    //     });
    //   }
    // } catch (error) {
    //   setLoading(false);
    //   toast.error(error?.response?.data?.message + "in eventnamechartreport.");
    //   setEventCountNameData({
    //     labels: [],
    //     datasets: [],
    //   });
    // }
  };

  useEffect(() => {
    if (DateFilterValue) {
      getEventsReport();
    }
  }, [DateFilterValue]);

  return (
    <div className="box-shadow">
      <Boxheading title="Event Count by Event Name" />
      {Loading ? (
        <LoadingComp />
      ) : (
        <Line options={options} data={EventCountNameData} />
      )}
    </div>
  );
};

export default TrafficTabChart;
